import Vue from "vue";
import axios from "axios";
import * as websocket from "../../scripts/websocket.js";

const state = {
  active_test_id: null,
  active_chat: null,
  tests: {},
  users: {},
  latest_announcement: {
    show: false,
    message: {
      content: "test content",
    },
  },
  CHAT_ANNOUNCEMENTS_NAME: "Announcements",
  CHAT_ANNOUNCEMENTS: 0,
  CHAT_GROUP: 1,
  CHAT_GROUP_NAME: "Teacher chat",
  CHAT_ONETOONE: 2,
};

const getters = {
  active_test: (state) => {
    return state.tests[state.active_test_id];
  },
};

const actions = {
  async loadTest({ commit, dispatch }, { test_id }) {
    axios
      .get("/tests/" + test_id)
      .then(function(response) {
        // set the test object to the response
        // console.log("loadTest", response.data);
        for (const chat of Object.values(response.data.chats)) {
          chat.type = state.CHAT_ONETOONE;
        }
        if (
          response.data.chats &&
          response.data.chats[state.CHAT_ANNOUNCEMENTS_NAME]
        ) {
          response.data.chats[state.CHAT_ANNOUNCEMENTS_NAME].type =
            state.CHAT_ANNOUNCEMENTS;
        }
        if (response.data.chats && response.data.chats[state.CHAT_GROUP_NAME]) {
          response.data.chats[state.CHAT_GROUP_NAME].type = state.CHAT_GROUP;
        }

        commit("setTest", {
          test_id: response.data.test_id,
          test: response.data,
        });
      })
      .catch((error) => {
        dispatch(
          "throwError",
          {
            error: "Error while getting test info " + error,
          },
          { root: true }
        );
      });

    // TODO last chat message is not present in API response
  },
  async loadUsers({ commit, dispatch }, { test_id }) {
    // TODO change to the people object of the test
    axios
      .get("/tests/" + test_id + "/users")
      .then(function(response) {
        let students = response.data.students;
        if (students) {
          if (Object.keys(students).length !== 0) {
            commit("addUsers", { users: students });
          }
        } else {
          dispatch(
            "throwError",
            {
              error: "Error while getting test users: no students object",
            },
            { root: true }
          );
        }
        let teachers = response.data.teachers;
        for (const teacher in teachers) {
          teachers[teacher].is_supervisor = true;
        }
        if (teachers) {
          if (Object.keys(teachers).length !== 0) {
            commit("addUsers", { users: teachers });
          }
        } else {
          dispatch(
            "throwError",
            {
              error: "Error while getting test users: no teachers object",
            },
            { root: true }
          );
        }
      })
      .catch((error) => {
        dispatch(
          "throwError",
          {
            error: "Error while getting test users: " + error,
          },
          { root: true }
        );
      });
  },
  async sendWsMessage({ dispatch }, { message }) {
    // sent to server
    let sentOverWebsocket = websocket.sendToServer(message);
    if (!sentOverWebsocket) {
      dispatch(
        "throwError",
        {
          error: "Error while sending message:s ending over websocket failed",
        },
        { root: true }
      );
    } else {
      // TODO websocket doesn't exist or is not open anymore, retry?
    }
  },

  receivedWsMessage({ dispatch }, message) {
    if (!message.messageType) {
      dispatch(
        "throwError",
        {
          error:
            "Error on received websocket message: no message type specified",
        },
        { root: true }
      );
    }
    if (!message.receiverIds) {
      // TODO check if receiver is you
      dispatch(
        "throwError",
        {
          error:
            "Error on received websocket message: this message was not intended for you",
        },
        { root: true }
      );
    }
    switch (message.messageType) {
      case "message_final":
        dispatch("receivedChatMessage", { message: message.message });
        break;
      case "conversation_unread":
        dispatch("receivedMessageUnread", { message: message.message });
        break;
      case "conversation_assigned":
        dispatch("receivedMessageAssigned", { message: message.message });
        break;
      case "message_delete":
        dispatch("receivedMessageDeleted", { message: message.message });
        break;
      case "message_edit":
        dispatch("receivedChatMessage", { message: message.message });
        break;
      case "conversation_typing":
        dispatch("receivedConversationTyping", { message: message.message });
        break;
    }
  },
  receivedChatMessage({ commit, dispatch }, { message }) {
    console.log("test.js receivedChatMessage", message);
    // We received a chat message
    if (
      typeof message.test_id === "undefined" ||
      message.test_id == null ||
      !state.tests[message.test_id]
    ) {
      dispatch(
        "throwError",
        {
          error:
            "Error on received message: received a message for a test that doesn't exist for this user",
        },
        { root: true }
      );
      return;
    }
    if (
      typeof message.chat_id === "undefined" ||
      message.chat_id == null ||
      !state.tests[message.test_id].chats[message.chat_id]
    ) {
      dispatch(
        "throwError",
        {
          error:
            "Error on received message: received a message for a chat that doesn't exist for this user",
        },
        { root: true }
      );
      return;
    }
    if (typeof message.sender_id === "undefined" || message.sender_id == null) {
      // TODO check if sender corresponds to local DB
      dispatch(
        "throwError",
        {
          error:
            "Error on received message: received a message for a sender that doesn't exist for this user",
        },
        { root: true }
      );
      return;
    }
    if (
      typeof message.message_id === "undefined" ||
      message.message_id == null ||
      !message.timestamp ||
      !message.content
    ) {
      dispatch(
        "throwError",
        {
          error:
            "Error on received message: received a message where not all required fields are present",
        },
        { root: true }
      );
    }
    // Check if chat is not active, and message is a new message, to increment chat unread
    if (!state.active_chat || state.active_chat.chat_id != message.chat_id) {
      if (
        !state.tests[message.test_id].chats[message.chat_id].messages[
          message.sender_id + ":" + message.message_id
        ]
      ) {
        // if not an update, increment unread chat
        commit("incrementChatUnread", {
          chat: state.tests[message.test_id].chats[message.chat_id],
        });
      }
    } else {
      // or if the chat is active, maybe we should notify the other teachers
      if (
        state.active_chat.name !== state.CHAT_ANNOUNCEMENTS_NAME &&
        state.active_chat.name !== state.CHAT_GROUP_NAME
      ) {
        dispatch("updateChatUnread", {
          chat: state.tests[message.test_id].chats[message.chat_id],
          number_of_unread: 0, // TODO this should be increment, not absolute 1
          force: true,
        });
      }
    }

    // Received a chat message without any errors, so add it to the state
    commit("addChatMessage", { message });

    console.log(
      state.tests[message.test_id].chats[message.chat_id].messages[
        message.sender_id + ":" + message.message_id
      ]
    );
    // The messsage does not exist (not an edit), so update latest chat message
    if (
      !state.tests[message.test_id].chats[message.chat_id].messages[
        message.sender_id + ":" + message.message_id
      ]
    ) {
      commit("setLatestChatMessage", { message });
    }

    // make a notification

    // it's a new announcement
    if (
      state.tests[message.test_id].chats[message.chat_id].name ===
      state.CHAT_ANNOUNCEMENTS_NAME
    ) {
      commit("setLatestAnnouncementMessage", { message });
      commit("setLatestAnnouncementShow", { show: true });
      dispatch(
        "showDesktopNotification",
        { content: "New announcement" },
        { root: true }
      );
    }
  },
  receivedMessageUnread({ commit, dispatch }, { message }) {
    if (
      typeof message.test_id === "undefined" ||
      message.test_id == null ||
      !state.tests[message.test_id]
    ) {
      dispatch(
        "throwError",
        {
          error:
            "Error on received message unread: received a message unread update for a test that doesn't exist for this user",
        },
        { root: true }
      );
      return;
    }
    if (
      typeof message.chat_id === "undefined" ||
      message.chat_id == null ||
      !state.tests[message.test_id].chats[message.chat_id]
    ) {
      dispatch(
        "throwError",
        {
          error:
            "Error on received message unread: received a message unread update for a chat that doesn't exist for this user",
        },
        { root: true }
      );
      return;
    }
    if (message.unread !== undefined) {
      if (!state.active_chat || state.active_chat.chat_id != message.chat_id) {
        commit("setChatUnread", {
          chat: state.tests[message.test_id].chats[message.chat_id],
          number_of_unread: message.unread,
        });
      }
    }
  },
  receivedMessageAssigned({ commit, dispatch }, { message }) {
    if (!message.test_id || !state.tests[message.test_id]) {
      dispatch(
        "throwError",
        {
          error:
            "Error on received assigned chat: received an update for a test that doesn't exist for this user",
        },
        { root: true }
      );
      return;
    }
    if (
      !message.chat_id ||
      !state.tests[message.test_id].chats[message.chat_id]
    ) {
      dispatch(
        "throwError",
        {
          error:
            "Error on received assigned chat: received an update for a chat that doesn't exist for this user",
        },
        { root: true }
      );
      return;
    }
    if (
      !message.user_id ||
      (message.user_id !== "NULL" &&
        !state.tests[message.test_id].chats[message.chat_id].people[
          message.user_id
        ])
    ) {
      dispatch(
        "throwError",
        {
          error:
            "Error on received assigned chat: received an update for a conversation that doesn't exist for this user",
        },
        { root: true }
      );
      return;
    }
    if (message.user_id === "NULL") {
      // Removed the assignment
      commit("setChatAssigned", {
        chat: state.tests[message.test_id].chats[message.chat_id],
        name: null,
      });
    } else {
      // assign a user to the chat
      let name =
        state.tests[message.test_id].chats[message.chat_id].people[
          message.user_id
        ].name;
      commit("setChatAssigned", {
        chat: state.tests[message.test_id].chats[message.chat_id],
        name: name.substr(0, name.indexOf(",")),
      });
    }
  },
  receivedMessageDeleted({ commit, dispatch }, { message }) {
    if (!message.test_id || !state.tests[message.test_id]) {
      dispatch(
        "throwError",
        {
          error:
            "Error on received deleted message: received an update for a test that doesn't exist for this user",
        },
        { root: true }
      );

      return;
    }
    if (
      !message.chat_id ||
      !state.tests[message.test_id].chats[message.chat_id]
    ) {
      dispatch(
        "throwError",
        {
          error:
            "Error on received deleted message: received an update for a chat that doesn't exist for this user",
        },
        { root: true }
      );
      return;
    }
    if (!message.sender_id) {
      dispatch(
        "throwError",
        {
          error:
            "Error on received deleted message: received a message where not all required fields are present",
        },
        { root: true }
      );
      return;
    }
    if (
      !message.message_id ||
      !state.tests[message.test_id].chats[message.chat_id].messages[
        message.sender_id + ":" + message.message_id
      ]
    ) {
      dispatch(
        "throwError",
        {
          error:
            "Error on received deleted message: received an update for a message that doesn't exist for this user",
        },
        { root: true }
      );
      return;
    }
    commit("removeChatMessage", {
      message:
        state.tests[message.test_id].chats[message.chat_id].messages[
          message.sender_id + ":" + message.message_id
        ],
    });
    if (
      state.tests[message.test_id].chats[message.chat_id].name ===
      state.CHAT_ANNOUNCEMENTS_NAME
    ) {
      if (message.message_id === state.latest_announcement.message.message_id) {
        // the latest announcement has been deleted, so hide the pop-up
        commit("setLatestAnnouncementShow", { show: false });
      }
    }
  },
  updateChatUnread(
    { commit, dispatch, getters, rootState },
    { chat, number_of_unread, force }
  ) {
    if ((chat.unread != number_of_unread) | (force === true)) {
      commit("setChatUnread", { chat, number_of_unread });
      if (
        chat.name != state.CHAT_ANNOUNCEMENTS_NAME &&
        chat.name != state.CHAT_GROUP_NAME &&
        getters.active_test.people[rootState.app.current_user.user_id]
          .is_supervisor
      ) {
        let people = Object.values(chat.people);

        let receivers = [];
        for (const recipient in people) {
          if (
            getters.active_test.people[people[recipient].user_id] &&
            getters.active_test.people[people[recipient].user_id]
              .is_supervisor === true
          ) {
            receivers.push("" + people[recipient].user_id);
          }
        }

        let msg_wrapped = {
          messageType: "conversation_unread",
          receiverIds: receivers,
          message: {
            test_id: chat.test_id,
            chat_id: chat.chat_id,
            unread: number_of_unread,
          },
        };
        // Send message over websocket
        dispatch("sendWsMessage", {
          message: msg_wrapped,
        });
      }
    }
  },
  receivedConversationTyping({ commit, dispatch }, { message }) {
    if (!message.test_id || !state.tests[message.test_id]) {
      dispatch(
        "throwError",
        {
          error:
            "Error on received conversation typing: received an update for a test that doesn't exist for this user",
        },
        { root: true }
      );
      return;
    }
    if (
      !message.chat_id ||
      !state.tests[message.test_id].chats[message.chat_id]
    ) {
      dispatch(
        "throwError",
        {
          error:
            "Error on received conversation typing: received an update for a chat that doesn't exist for this user",
        },
        { root: true }
      );
      return;
    }
    if (
      !message.user_id ||
      message.user_id === "NULL" ||
      !state.tests[message.test_id].chats[message.chat_id].people[
        message.user_id
      ]
    ) {
      dispatch(
        "throwError",
        {
          error:
            "Error on received conversation typing: received an update for a conversation that doesn't exist for this user",
        },
        { root: true }
      );
      return;
    }

    if (message.typing) {
      // set user typing
      commit("addChatTyping", {
        chat: state.tests[message.test_id].chats[message.chat_id],
        user_id: message.user_id,
      });
    } else {
      // remove user typing
      commit("removeChatTyping", {
        chat: state.tests[message.test_id].chats[message.chat_id],
        user_id: message.user_id,
      });
    }
  },
};

const mutations = {
  // TODO use Vue.set for functions below?
  setActiveTestId(state, test_id) {
    state.active_test_id = parseInt(test_id);
  },
  setActiveChatById(state, chat_id) {
    state.active_chat = state.tests[state.active_test_id].chats[chat_id];
  },
  setActiveChat(state, chat) {
    state.active_chat = chat;
  },

  setTest(state, { test_id, test }) {
    Vue.set(state.tests, test_id, test);
  },
  setTestName(state, { test_id, name }) {
    Vue.set(state.tests[test_id], "name", name);
  },
  setTestChats(state, { test_id, chats }) {
    Vue.set(state.tests[test_id], "chats", chats);
  },
  setUsers(state, users) {
    state.users = users;
  },

  setChatUnread(state, { chat, number_of_unread }) {
    Vue.set(chat, "unread", number_of_unread);
  },
  setChatAssigned(state, { chat, name }) {
    Vue.set(chat, "assigned", name);
  },
  incrementChatUnread(state, { chat }) {
    if (chat.number_of_unread) {
      Vue.set(chat, "unread", chat.number_of_unread + 1);
    } else {
      Vue.set(chat, "unread", 1);
    }
  },
  decrementChatUnread(state, { chat }) {
    if (chat.number_of_unread && chat.number_of_unread > 0) {
      Vue.set(chat, "unread", chat.number_of_unread - 1);
    } else {
      Vue.set(chat, "unread", 0);
    }
  },
  addChatTyping(state, { chat, user_id }) {
    if (!chat.typing) {
      Vue.set(chat, "typing", {});
    }
    Vue.set(chat.typing, user_id, true);
  },
  removeChatTyping(state, { chat, user_id }) {
    if (chat.typing && chat.typing[user_id]) {
      Vue.set(chat.typing, user_id, false);
    }
  },
  addChat(state, { chat }) {
    Vue.set(state.tests[chat.test_id].chats, chat.id, chat);
  },
  addChatMessage(state, { message }) {
    console.log("addChatMessage", message);
    Vue.set(
      state.tests[message.test_id].chats[message.chat_id].messages,
      message.sender_id + ":" + message.message_id,
      message
    );
  },
  setLatestChatMessage(state, { message }) {
    Vue.set(
      state.tests[message.test_id].chats[message.chat_id],
      "newest_message",
      message.sender_id + ":" + message.message_id
    );
  },
  removeChatMessage(state, { message }) {
    Vue.set(message, "visible", false);
    Vue.set(message, "content", "This message has been deleted");
  },
  addUsers(state, { users }) {
    // TODO is this reactive?
    state.users = { ...state.users, ...users };
  },
  setLatestAnnouncementMessage(state, { message }) {
    Vue.set(state.latest_announcement, "message", message);
  },
  setLatestAnnouncementShow(state, { show }) {
    Vue.set(state.latest_announcement, "show", show);
  },
};

export default {
  state,
  getters,
  actions,
  mutations,
};
