<template>
  <div>
    <div>
      <b-nav-item
        :to="{ name: link.path }"
        v-for="link in variableLinksToShow"
        :key="link.path"
        append
        >{{ link.name }}</b-nav-item
      >
    </div>
    <br /><br /><br />
    <div>
      <b-nav-item
        :href="link.href ? link.href : null"
        :to="!link.href ? { name: link.path } : null"
        v-for="link in constantLinks"
        :key="link.path"
        append
        >{{ link.name }}</b-nav-item
      >
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState } from "vuex";
export default {
  name: "NavItem",
  computed: {
    ...mapGetters(["active_test"]),
    ...mapState({
      current_user_id: (state) => state.app.current_user.user_id,
    }),
  },
  watch: {
    $route: {
      immediate: true,
      handler() {
        this.updateVariableLinksToShow();
      },
    },
    active_test: function() {
      this.updateVariableLinksToShow();
    },
    current_user_id: function() {
      this.updateVariableLinksToShow();
    },
  },
  methods: {
    updateVariableLinksToShow() {
      this.variableLinksToShow = this.variableLinks.filter((item) => {
        return (
          item.whitelist.includes(this.$route.name) &&
          (item.need_supervisor !== true ||
            (this.active_test &&
              this.current_user_id &&
              this.active_test.people[this.current_user_id].is_supervisor ===
                true))
        );
      });
    },
  },
  data() {
    return {
      variableLinksToShow: [],
      variableLinks: [
        {
          name: "Chat",
          path: "test-chat",
          whitelist: ["test-chat", "test-people", "test-settings"],
        },
        {
          name: "People",
          path: "test-people",
          whitelist: ["test-chat", "test-people", "test-settings"],
          need_supervisor: true,
        },
        {
          name: "Settings",
          path: "test-settings",
          whitelist: ["test-chat", "test-people", "test-settings"],
          need_supervisor: true,
        },
        {
          name: "Settings",
          path: "course",
          whitelist: ["course", "course-settings"],
        },
      ],
      constantLinks: [
        {
          name: "Courses",
          path: "courses",
        },
        {
          name: "Logout",
          href: "/login?logout",
          //path: null, // TODO actually log out
        },
      ],
    };
  },
};
</script>

