<template>
  <div class="container">
    <p v-if="Object.keys(courses).length == 0">
      No courses/tests found :( #TODO nice display
    </p>
    <div
      class="accordion"
      role="tablist"
      v-if="Object.keys(courses).length !== 0"
    >
      <b-card
        no-body
        class="mb-1"
        v-for="course in courses"
        :key="course.canvas_id"
      >
        <b-card-header header-tag="header" class="p-1" role="tab">
          <b-button
            block
            v-b-toggle="'course-' + course.canvas_id"
            variant="info"
            >{{ course.name }}
            <b-dropdown
              class="dropdown-dropleft float-right"
              dropleft
              variant="link"
              no-caret
              v-if="current_user.is_employee"
            >
              <template #button-content>
                <b-icon
                  class="icon-settings"
                  icon="three-dots-vertical"
                ></b-icon>
              </template>
              <b-dropdown-item
                v-on:click.stop="onCourseDetails(course)"
                href="#"
                >Course details</b-dropdown-item
              >
            </b-dropdown>
          </b-button>
        </b-card-header>
        <b-collapse
          v-bind:id="'course-' + course.canvas_id"
          visible
          accordion="courses-accordion"
          role="tabpanel"
        >
          <b-card-body>
            <b-table
              striped
              hover
              :items="Object.values(course.tests)"
              :fields="module_fields"
              @row-clicked="testClickedHandler"
            >
              <template #cell(open)="data">
                <b-button size="sm" @click="testClickedHandler(data.item)">
                  Open test
                  <b-icon
                    class="icon-play"
                    icon="play-fill"
                    aria-hidden="true"
                  ></b-icon>
                </b-button>
              </template>
            </b-table>
          </b-card-body>
        </b-collapse>
      </b-card>
    </div>
    <b-button v-if="current_user.is_employee" :to="{ name: 'course-import' }"
      >New course</b-button
    >
  </div>
</template>

<style scoped>
.dropdown-dropleft button svg {
  color: #fff;
}
</style>

<script>
import { mapState, mapActions } from "vuex";

export default {
  name: "Courses",
  components: {},
  data() {
    return {
      module_fields: ["name", "open"],
      courses: {},
    };
  },
  methods: {
    ...mapActions(["throwError"]),
    testClickedHandler: function(item) {
      this.$router.push({ name: "test-chat", params: { id: item.test_id } });
    },
    loadCourses: function() {
      this.$http
        .get("/courses")
        .then((response) => {
          if (response.data.courses) {
            this.courses = response.data.courses;
            if (!this.current_user.is_employee) {
              if (Object.values(this.courses).length === 1) {
                // only one active course for you
                if (
                  Object.values(Object.values(this.courses)[0].tests).length ===
                  1 && this.$route.path === "/"
                ) {
                  // and that course has only one test, so redirect
                  this.$router.push({
                    name: "test-chat",
                    params: {
                      id: Object.values(Object.values(this.courses)[0].tests)[0]
                        .test_id,
                    },
                  });
                }
              }
            }

            console.log(
              "loadCourses",
              this.courses,
              Object.values(Object.values(this.courses)[0].tests).length
            );
          } else {
            this.courses = {};
          }
        })
        .catch((error) => {
          this.throwError({
            error: "Error while loading courses: " + error,
          });
        });
    },
    onCourseDetails: function(item) {
      this.$router.push({ name: "course", params: { id: item.canvas_id } });
    },
  },
  computed: {
    ...mapState({
      current_user: (state) => state.app.current_user,
    }),
  },
  created: function() {
    this.loadCourses();
  },
};
</script>
