<template>
  <div class="container">
    <h1>
      People
      <small class="text-muted" v-if="active_test">{{
        active_test.name
      }}</small>
    </h1>
    <p>In here you can change the people and their permissions.</p>
    <b-container>
      <b-row>
        <b-col>
          <b-list-group flush>
            <h4>Students</h4>
            <div class="people_list">
              <b-list-group-item
                v-for="student in students"
                :key="student.id"
                href="#"
                class="d-flex justify-content-between align-items-center"
                :variant="student.variant"
              >
                {{ student.name }}
                <b-dropdown
                  class="dropdown-dropright"
                  dropright
                  variant="link"
                  no-caret
                >
                  <template #button-content>
                    <b-icon
                      class="icon-settings"
                      icon="three-dots-vertical"
                    ></b-icon>
                  </template>
                  <b-dropdown-item
                    v-on:click.stop="onRemoveStudent(student)"
                    href="#"
                    >Remove student</b-dropdown-item
                  >
                </b-dropdown>
              </b-list-group-item>
            </div>
          </b-list-group>
        </b-col>
        <b-col>
          <b-list-group flush>
            <h4>Supervisors</h4>
            <div class="people_list">
              <b-list-group-item
                v-for="supervisor in supervisors"
                :key="supervisor.id"
                href="#"
                class="d-flex justify-content-between align-items-center"
              >
                {{ supervisor.name }}
                <b-dropdown
                  class="dropdown-dropright"
                  dropright
                  variant="link"
                  no-caret
                >
                  <template #button-content>
                    <b-icon
                      class="icon-settings"
                      icon="three-dots-vertical"
                    ></b-icon>
                  </template>
                  <b-dropdown-item
                    v-on:click.stop="onRemoveSupervisor(supervisor)"
                    href="#"
                    >Remove supervisor</b-dropdown-item
                  >
                </b-dropdown>
              </b-list-group-item>
            </div>
          </b-list-group>
        </b-col>
        <b-col>
          <b-list-group flush>
            <h4>Module coordinator</h4>
            <b-list-group-item href="#">
              {{ module_coordinator.name }}
            </b-list-group-item>
          </b-list-group>
        </b-col>
      </b-row>
      <hr />
      <b-row>
        <b-col>
          <h4>Add new enrollment</h4>
          <b-form-input
            id="input-user"
            type="text"
            placeholder="Search on m-number, name or email"
            list="user-search-list"
            v-model="search"
            debounce="500"
          ></b-form-input>

          <div class="d-flex justify-content-center mt-2" v-if="loadingSearch">
            <b-spinner label="Loading..."></b-spinner>
          </div>
          <b-list-group class="mt-2 mb-4" v-else>
            <b-list-group-item
              v-for="user in searchResults"
              :key="user.id"
              href="#"
            >
              {{ user.name }}
              <small>- {{ user.id }}, {{ user.email }}</small>
              <b-dropdown
                class="dropdown-dropright"
                dropright
                variant="link"
                no-caret
              >
                <template #button-content>
                  <b-icon
                    class="icon-settings"
                    icon="three-dots-vertical"
                  ></b-icon>
                </template>
                <b-dropdown-item v-on:click.stop="onAddStudent(user)" href="#"
                  >Add as student</b-dropdown-item
                >
                <b-dropdown-item
                  v-on:click.stop="onAddSupervisor(user)"
                  href="#"
                  >Add as supervisor</b-dropdown-item
                >
              </b-dropdown>
            </b-list-group-item>
          </b-list-group>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>
<style scoped>
.people_list {
  height: 500px;
  overflow-y: scroll;
}
</style>
<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "TestPeople",
  data() {
    return {
      students: {},
      supervisors: {},
      module_coordinator: {},
      search: "",
      loadingSearch: false,
      searchResults: [],
    };
  },
  methods: {
    ...mapActions(["throwError"]),
    onAddStudent(user) {
      if (user.user_id) {
        let putData = { role: "S" };
        this.$http
          .post(
            "/tests/" + this.$route.params.id + "/users/" + user.user_id,
            putData
          )
          .then(() => {
            this.$set(this.students, user.user_id, user);
            // TODO no one knows about this yet
            this.throwError({
              error:
                "Added this user to the test, but be aware that other users only receive this update on refresh",
            });
          })
          .catch((error) => {
            this.throwError({
              error: "Error while adding supervisor: " + error,
            });
          });
      } else {
        this.throwError({
          error: "Error while adding supervisor: invalid user",
        });
      }
    },
    onAddSupervisor(user) {
      if (user.user_id) {
        let putData = { role: "T" };
        this.$http
          .post(
            "/tests/" + this.$route.params.id + "/users/" + user.user_id,
            putData
          )
          .then(() => {
            // TODO feedback user is imported
            this.$set(this.supervisors, user.user_id, user);
            this.throwError({
              error:
                "Added this supervisor to the test, but be aware that other users only receive this update on refresh",
            });
          })
          .catch((error) => {
            this.throwError({
              error: "Error while adding student: " + error,
            });
          });
      } else {
        this.throwError({
          error: "Error while adding student: invalid user",
        });
      }
    },
    onRemoveStudent(student) {
      // this.$set(this.students[student.user_id], "variant", "danger");
      this.$http
        .delete("/tests/" + this.$route.params.id + "/users/" + student.user_id)
        .then(() => {
          this.$delete(this.students, student.user_id);
        })
        .catch((error) => {
          this.throwError({
            error: "Error while removing student enrollment: " + error,
          });
        });
    },
    onRemoveSupervisor(supervisor) {
      this.$http
        .delete(
          "/tests/" + this.$route.params.id + "/users/" + supervisor.user_id
        )
        .then(() => {
          this.$delete(this.supervisors, supervisor.user_id);
        })
        .catch((error) => {
          this.throwError({
            error: "Error while removing supervisor enrollment: " + error,
          });
        });
    },
    getPeople() {
      this.$http
        .get("/tests/" + this.$route.params.id + "/users")
        .then((response) => {
          if (response.data.students) {
            this.students = response.data.students;
          } else {
            this.throwError({
              error:
                "Error while getting test people: students object not present",
            });
          }
          if (response.data.teachers) {
            this.supervisors = response.data.teachers;
          } else {
            this.throwError({
              error:
                "Error while getting test people: teachers object not present",
            });
          }
          if (response.data.module_coordinator) {
            this.module_coordinator = response.data.module_coordinator;
          } else {
            this.throwError({
              error:
                "Error while getting test people: module_coordinator object not present",
            });
          }
        })
        .catch((error) =>
          this.throwError({
            error: "Error while getting test people: " + error,
          })
        );
    },
    searchUser(val) {
      this.loadingSearch = true;

      this.$http
        .get("/users/?q=" + val, this.form)
        .then((response) => {
          this.loadingSearch = false;
          if (response.data.users) {
            this.searchResults = response.data.users;
          } else {
            this.throwError({
              error:
                "Error while getting users: cannot extract data from response",
            });
          }
        })
        .catch((error) => {
          this.loadingSearch = false;
          this.throwError({
            error: "Error while getting users: " + error,
          });
        });
    },
  },
  computed: {
    ...mapGetters(["active_test"]),
  },
  watch: {
    search: function(newVal) {
      if (!newVal || newVal.length < 4) {
        return null;
      }
      this.searchUser(newVal);
    },
  },
  mounted: function() {
    this.getPeople();
  },
};
</script>

<style></style>
