<template>
  <div>
    <Navbar />
    <div class="wrapper">
      <router-view />
    </div>

    <b-modal id="error-modal" centered @hidden="unsetError()">
      <template #modal-header>
        <h5 class="p-0 m-0" text-danger>An error occured</h5>
      </template>
      <p class="my-4">{{ error }}</p>
      <template #modal-footer>
        <b-button type="submit" variant="info" @click="unsetError()"
          >Close</b-button
        >
      </template>
    </b-modal>
  </div>
</template>

<style>
.wrapper {
  padding-top: 65px;
}

/* Medium devices (tablets, 768px and up) 
  Notice, width is hardcoded from default bootstrap configuration
*/
@media (min-width: 576px) {
  .wrapper {
    margin-left: 100px;
  }
}
</style>

<script>
import { mapMutations, mapState, mapActions } from "vuex";
import Navbar from "../components/navigation/Navbar.vue";

export default {
  name: "Dashboard",
  components: {
    Navbar,
  },
  created: function() {
    this.loadCurrentUser();
  },
  watch: {
    error: function(newVal) {
      if (newVal != null) {
        // open the modal
        this.$bvModal.show("error-modal");
      } else {
        // close the modal
        this.$bvModal.hide("error-modal");
      }
    },
  },
  computed: {
    ...mapState({
      error: (state) => state.app.error,
    }),
  },
  mounted: function() {
    this.$http.defaults.headers.common["X-CSRF-TOKEN"] = document.querySelector(
      'meta[name="_csrf"]'
    ).content;
    this.askDesktopNotificationPermission();
  },
  methods: {
    ...mapMutations(["unsetError", "setCurrentUser"]),
    ...mapActions(["askDesktopNotificationPermission", "throwError"]),
    loadCurrentUser() {
      this.axios
        .get("/users/me")
        .then((response) => {
          let user = response.data;
          user.user_id = "" + user.user_id;
          this.setCurrentUser(user);
        })
        .catch((error) =>
          this.throwError({
            error: "Error while getting current user data" + error,
          })
        );
    },
  },
};
</script>
