<template>
  <div class="container">
    <h1>
      Settings
      <small class="text-muted" v-if="active_test">{{
        active_test.name
      }}</small>
    </h1>
    <p>In here you can change settings for this specific test.</p>
    <b-form @submit="onSubmit">
      <!-- TODO only show name change if is module coordinator -->
      <b-form-group id="input-group-name" label="Name:" label-for="input-name">
        <b-form-input
          id="input-name"
          v-model="form.name"
          type="text"
          placeholder="Enter test name"
          required
        ></b-form-input>
      </b-form-group>
      <!-- <b-form-group
        id="input-group-start"
        label="Start"
        label-for="input-start-date"
      >
