            <b-form-datepicker
              id="input-start-date"
              v-model="formStartDate"
              type="text"
              placeholder="Enter start date"
              class="col"
              value-as-date
            ></b-form-datepicker>
          </b-col>
          <b-col>
            <b-form-timepicker
              id="input-start-time"
              v-model="formStartTime"
              type="text"
              placeholder="Enter start time"
              class="col"
            ></b-form-timepicker>
          </b-col>
        </b-row>
      </b-form-group>
      <b-form-group id="input-group-end" label="End" label-for="input-end-date">
        <b-row>
          <b-col>
            <b-form-datepicker
              id="input-end-date"
              v-model="formEndDate"
              type="text"
