            <b-form-timepicker
              id="input-end-time"
              v-model="formEndTime"
              type="text"
              placeholder="Enter end time"
              class="col"
            ></b-form-timepicker>
          </b-col>
        </b-row>
      </b-form-group> -->
      <b-button type="submit" variant="info">Save</b-button>
    </b-form>
    <hr />
    <b-button v-if="!test_is_open" @click="onStartTest">
      Start test
      <b-spinner small v-if="loading.start_test"></b-spinner
    ></b-button>
    <b-button v-if="test_is_open" class="ml-2" @click="onStopTest">
      Stop test
      <b-spinner small v-if="loading.stop_test"></b-spinner></b-button
    ><br />
    <hr />
    <b-button class="my-2" disabled>Download chat backlog</b-button><br />
    <b-button disabled variant="danger">Delete complete test log</b-button>
  </div>
</template>

<script>
import { mapGetters, mapMutations, mapState, mapActions } from "vuex";
export default {
  name: "TestSettings",
  data() {
    return {
      initial_form: null,
      form: {
        test_id: null,
        name: "",
        // start_date: "",
        // start_time: "",
        // end_date: "",
        // end_time: "",
      },
      loading: {
        start_test: false,
        stop_test: false,
      },
    };
  },
  computed: {
    ...mapGetters(["active_test"]),
    ...mapState({
      current_user: (state) => state.app.current_user,
    }),
    test_is_open: function() {
      return (
        this.active_test &&
        this.active_test.start_time !== null &&
        this.active_test.end_time === null
      );
    },
  },
  methods: {
    ...mapMutations(["setTestName"]),
    ...mapActions(["throwError", "loadTest"]),
    onSubmit(event) {
      event.preventDefault();
      let changedData = this.formFieldsChanged();
      if (changedData.length !== 0) {
        this.$http
          .put("/tests/" + this.$route.params.id, this.form)
          .then(() => {
            if ("name" in changedData) {
              this.setTestName({
                test_id: this.form.test_id,
                name: changedData["name"],
              });
            }
          })
          .catch((error) => {
            this.throwError({
              error: "Error while updating test: " + error,
            });
          });
      } else {
        alert("nothin changed lol");
      }
    },
    formFieldsChanged() {
      let k = Object.keys(this.form).filter(
        (key) => this.form[key] !== this.initial_form[key]
      );
      return k.reduce((obj, key) => {
        obj[key] = this.form[key];
        return obj;
      }, {});
    },
    onStartTest() {
      // TODO reopen?
      this.loading.start_test = true;
      this.$http
        .put("/tests/" + this.$route.params.id + "/open")
        .then(() => {
          this.loading.start_test = false;
          this.loadTest({ test_id: this.$route.params.id });
        })
        .catch((error) => {
          this.loading.start_test = false;
          this.throwError({
            error: "Error while starting test: " + error,
          });
        });
    },
    onStopTest() {
      this.loading.stop_test = true;
      this.$http
        .put("/tests/" + this.$route.params.id + "/close")
        .then(() => {
          this.loading.stop_test = false;
        })
        .catch((error) => {
          this.loading.stop_test = false;
          this.throwError({
            error: "Error while stopping test: " + error,
          });
        });
    },
  },
  created: function() {},
  watch: {
    active_test: function(newVal) {
      this.form.name = newVal.name;
      this.form.test_id = newVal.test_id;
      // this.form.start_date = new Date(newVal.start_time);
      // this.form.start_time = new Date(newVal.start_time).toLocaleTimeString(
      //   "nl-NL"
      // );
      // this.form.end_date = new Date(newVal.end_time);
      // this.form.end_time = new Date(newVal.end_time).toLocaleTimeString(
      //   "nl-NL"
      // );
      this.initial_form = JSON.parse(JSON.stringify(this.form));
    },
  },

  mounted: function() {
    if (this.active_test) {
      this.form.name = this.active_test.name;
      this.form.test_id = this.active_test.test_id;
    }
  },
};
</script>

