var socket;

export function startSocket() {
  // socket = new WebSocket("wss://sekhmet.chat/connectivityTestSocket");

  // socket = new WebSocket("ws://localhost:8081/socket/connectivityTestSocket");
  //location.origin.replace(/^http/, "ws") + "/socket/connectivityTestSocket"
  socket = new WebSocket(
    location.origin.replace(/^http/, "ws") + "/socket/mainSocket"
  );

  socket.onopen = () => {
    console.log("ws onopen");
  };

  socket.onclose = (event) => {
    if (event.wasClean) {
      console.log("websocket closed cleanly: ", event.code);
    } else {
      console.log("rip websocket", event);
    }
    if (event.code != 1000) {
      var xmlhttp = new XMLHttpRequest();
      xmlhttp.open("POST", "/api/error");
      xmlhttp.setRequestHeader(
        "Content-Type",
        "application/json;charset=UTF-8"
      );
      xmlhttp.send(
        JSON.stringify({
          timestamp: Date.now(),
          error: "websocket closed for suspicous reason",
          extra: {
            code: event.code,
            reason: event.reason,
          },
        })
      );
    }
    setTimeout(function() {
      console.log("Trying to reconnect to ws in 2 seconds...");
      startSocket();
    }, 2000);
  };
  socket.onerror = (error) => {
    var xmlhttp = new XMLHttpRequest();
    xmlhttp.open("POST", "/api/error");
    xmlhttp.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
    xmlhttp.send(
      JSON.stringify({
        timestamp: Date.now(),
        error: "websocket error",
        extra: error,
      })
    );
  };
}

export function addReceiveSwitch(filter) {
  socket.onmessage = (event) => {
    console.log("ws receiving:\n" + event.data);
    var json = JSON.parse(event.data);
    filter(json);
    //socket.onmessage(event);
  };
}

export function sendToServer(message) {
  if (socket && socket.readyState === 1) {
    console.log("ws sending:\n" + JSON.stringify(message));
    socket.send(JSON.stringify(message));
    return true;
  }
  console.log(
    "sendToServer called on a socket that is not open anymore...",
    socket
  );
  return false;
}
