<template>
  <b-container>
    <b-alert variant="info" :show="is_spectator">
      <h4 class="alert-heading">Spectator mode</h4>
      <p>
        You're not enrolled in this test, thus you cannot send any messages.
      </p>
    </b-alert>
    <h1>
      Test chat
      <small class="text-muted" v-if="active_test">{{
        active_test.name
      }}</small>
    </h1>
    <b-alert
      dismissible
      variant="success"
      :show="latest_announcement.show"
      @dismissed="hideLatestAnnouncement()"
    >
      <h4 class="alert-heading">New announcement!</h4>
      <p>
        {{ latest_announcement.message.content }}
      </p>
    </b-alert>
    <b-row>
      <b-col cols="4">
        <ChatList />
      </b-col>
      <b-col>
        <MessageList />
        <ChatInput />
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import { mapState, mapMutations, mapGetters } from "vuex";
import ChatList from "./ChatList.vue";
import ChatInput from "./ChatInput.vue";
import MessageList from "./MessageList.vue";

export default {
  name: "TestChat",
  data() {
    return {
      is_spectator: false,
    };
  },
  components: { ChatList, ChatInput, MessageList },
  computed: {
    ...mapState({
      current_user_id: (state) => state.app.current_user.user_id,
      latest_announcement: (state) => state.test.latest_announcement,
      tests: (state) => state.test.tests,
    }),
    ...mapGetters(["active_test"]),
  },
  methods: {
    ...mapMutations(["setLatestAnnouncementShow", "decrementChatUnread"]),
    hideLatestAnnouncement() {
      let test = this.tests[this.latest_announcement.message.test_id];
      if (test) {
        let chat = test.chats[this.latest_announcement.message.chat_id];
        if (chat) {
          this.decrementChatUnread({ chat });
        }
      }
      this.setLatestAnnouncementShow({ show: false });
    },
  },
  mounted: function() {
    // TODO set spectator
  },
};
</script>

